/**
*  
*/
<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/system/user' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>
		<!--  -->
		<el-form :inline="true" :model="formInline" class="user-search">
			<el-form-item label="">
				<el-select size="small" v-model="formInline.isLock" placeholder="">
					<el-option label="" value=""></el-option>
					<el-option label="" value="N"></el-option>
					<el-option label="" value="Y"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-input size="small" v-model="formInline.userName" placeholder=""></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input size="small" v-model="formInline.userMobile" placeholder=""></el-input>
			</el-form-item>
			<el-form-item>
				<el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
				<el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
				<!-- <el-button size="small" type="primary" @click="handleunit()"></el-button> -->
			</el-form-item>
		</el-form>
		<!---->
		<el-table size="small" @selection-change="selectChange" :data="userData" highlight-current-row v-loading="loading"
		 border element-loading-text="" style="width: 100%;">
			<el-table-column align="center" type="selection" width="50">
			</el-table-column>
			<!-- <el-table-column align="center" sortable prop="deptName" label="" width="120">
			</el-table-column> -->
			<el-table-column align="center" sortable prop="username" label="" width="120">
			</el-table-column>
			<!-- <el-table-column align="center" sortable prop="userRealName" label="" width="120">
			</el-table-column> -->
			<el-table-column align="center" sortable prop="phonenumber" label="" width="120">
			</el-table-column>
			<!-- <el-table-column align="center" sortable prop="userSex" label="" min-width="50">
			</el-table-column> -->
			<el-table-column align="center" sortable prop="email" label="" min-width="120">
			</el-table-column>
			<!-- <el-table-column align="center" sortable prop="editTime" label="" min-width="120">
				<template slot-scope="scope">
					<div>{{scope.row.editTime|timestampToTime}}</div>
				</template>
			</el-table-column> -->
			<el-table-column align="center" sortable prop="isLock" label="" min-width="50">
				<template slot-scope="scope">
					<el-switch v-model="scope.row.isLock=='N'?nshow:fshow" active-color="#13ce66" inactive-color="#ff4949" @change="editType(scope.$index, scope.row)">
					</el-switch>
				</template>
			</el-table-column>
			<el-table-column label="" min-width="300">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
					<el-button size="mini" type="success" @click="resetpwd(scope.$index, scope.row)"></el-button>
					<!-- <el-button size="mini" type="success" @click="dataAccess(scope.$index, scope.row)"></el-button> -->
					<!-- <el-button size="mini" type="success" @click="offlineUser(scope.$index, scope.row)"></el-button> -->
					<!-- <el-button size="mini" type="success" @click="refreshCache(scope.$index, scope.row)"></el-button> -->
				</template>
			</el-table-column>
		</el-table>
		<!--  -->
		<Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
		<!--  -->
		<el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
			<el-form label-width="80px" ref="editForm" :model="editForm" :rules="rules">
				<el-form-item label="" prop="userName">
					<el-input size="small" v-model="editForm.userName" auto-complete="off" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="userRealName">
					<el-input size="small" v-model="editForm.userRealName" auto-complete="off" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="roleId">
					<el-select size="small" v-model="editForm.roleId" placeholder="" class="userRole">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="userMobile">
					<el-input size="small" v-model="editForm.userMobile" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="userEmail">
					<el-input size="small" v-model="editForm.userEmail" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="userSex">
					<el-radio v-model="editForm.userSex" label=""></el-radio>
					<el-radio v-model="editForm.userSex" label=""></el-radio>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click='closeDialog("edit")'></el-button>
				<el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="dataAccessshow" width="30%" @click='closeDialog("perm")'>
			<el-tree ref="tree" default-expand-all="" :data="UserDept" :props="defaultProps" :default-checked-keys="checkmenu"
			 node-key="id" show-checkbox>
			</el-tree>
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click='closeDialog("perm")'></el-button>
				<el-button size="small" type="primary" :loading="loading" class="title" @click="menuPermSave"></el-button>
			</div>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="unitAccessshow" width="30%" @click='closeDialog("unit")'>
			<el-tree ref="tree" default-expand-all="" :data="UserDept" :props="defaultProps" @check-change="handleClick"
			 :default-checked-keys="checkmenu" node-key="id" show-checkbox check-strictly>
			</el-tree>
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click='closeDialog("unit")'></el-button>
				<el-button size="small" type="primary" :loading="loading" class="title" @click="unitPermSave"></el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	// 
	import {
		userList,
		userSave,
		userDelete,
		userPwd,
		userExpireToken,
		userFlashCache,
		userLock,
		UserDeptTree,
		UserDeptSave,
		UserDeptdeptTree,
		UserChangeDept
	} from '../../api/userMG'
	import Pagination from '../../components/Pagination'
	export default {
		data() {
			return {
				nshow: true, //switch
				fshow: false, //switch
				loading: false, //
				title: '',
				editFormVisible: false, //
				dataAccessshow: false, //
				unitAccessshow: false, //
				// 
				editForm: {
					userId: '',
					userName: '',
					userRealName: '',
					roleId: '',
					userMobile: '',
					userEmail: '',
					userSex: '',
					token: localStorage.getItem('logintoken')
				},
				// 
				unitparm: {
					userIds: '',
					deptId: '',
					deptName: ''
				},
				// 
				selectdata: [],
				// rules
				rules: {
					userName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					userRealName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					roleId: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					userMobile: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							pattern: /^1(3\d|47|5((?!4)\d)|7(0|1|[6-8])|8\d)\d{8,8}$/,
							required: true,
							message: '',
							trigger: 'blur'
						}
					],
					userEmail: [{
							required: true,
							message: '',
							trigger: 'blur'
						},
						{
							pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
							required: true,
							message: '',
							trigger: 'blur'
						}
					],
					userSex: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
				},
				// 
				seletedata: {
					ids: '',
					token: localStorage.getItem('logintoken')
				},
				// 
				resetpsd: {
					userId: '',
					token: localStorage.getItem('logintoken')
				},
				// 
				offline: {
					token: localStorage.getItem('logintoken')
				},
				// 
				formInline: {
					page: 1,
					limit: 10,
					deptId: '',
					userName: '',
					userMobile: '',
					isLock: ''
				},
				//
				userData: [],
				// 
				UserDept: [],
				defaultProps: {
					children: 'children',
					label: 'name'
				},
				// 
				checkmenu: [],
				//role
				saveroleId: '',
				// 
				pageparm: {
					currentPage: 1,
					pageSize: 10,
					total: 10
				}
			}
		},
		// 
		components: {
			Pagination
		},

		/**
		 * 
		 */
		watch: {},

		/**
		 * 
		 */
		created() {
			this.getdata(this.formInline)
		},

		/**
		 * 
		 */
		methods: {
			// 
			getdata(parameter) {
				this.loading = true
				// 
				// let res = {
				// 	code: 0,
				// 	msg: null,
				// 	count: 12,
				// 	data: [
				// 		{
							
				// 		}
						// {
						// 	addUser: '1',
						// 	editUser: '1',
						// 	addTime: null,
						// 	editTime: 1527411068000,
						// 	userId: 1,
						// 	systemNo: 'pmd',
						// 	userName: 'root',
						// 	userPassword: 'e10adc3949ba59abbe56e057f20f883e',
						// 	userRealName: '',
						// 	userSex: '',
						// 	userMobile: '138123456789',
						// 	userEmail: '111@qq.com',
						// 	isLock: 'N',
						// 	deptId: 1,
						// 	deptName: 'xxxx',
						// 	roleId: 1
						// }
				// 	]
				// }
				// this.loading = false
				// this.userData = res.data
				// // 
				// this.pageparm.currentPage = this.formInline.page
				// this.pageparm.pageSize = this.formInline.limit
				// this.pageparm.total = res.count
				// 

				/***
				 *  
				 */
				// 
				userList(parameter).then(res => {
				  this.loading = false
				  if (res == null) {
				    this.$message({
				      type: 'info',
				      message: res.msg
				    })
				  } else {
					console.log(res)
				    this.userData = res
				    // 
				    this.pageparm.currentPage = this.formInline.page
				    this.pageparm.pageSize = this.formInline.limit
				    this.pageparm.total = res.count
				  }
				})
			},
			// 
			callFather(parm) {
				this.formInline.page = parm.currentPage
				this.formInline.limit = parm.pageSize
				this.getdata(this.formInline)
			},
			//
			search() {
				this.getdata(this.formInline)
			},
			// type
			editType: function(index, row) {
				this.loading = true
				let parm = {
					lock: '',
					userId: '',
					token: localStorage.getItem('logintoken')
				}
				parm.userId = row.userId
				let lock = row.isLock
				if (lock == 'N') {
					parm.lock = 'Y'
				} else {
					parm.lock = 'N'
				}
				// 
				userLock(parm).then(res => {
					this.loading = false
					if (res.success == false) {
						this.$message({
							type: 'info',
							message: res.msg
						})
					} else {
						this.$message({
							type: 'success',
							message: ''
						})
						this.getdata(this.formInline)
					}
				})
			},
			//
			handleEdit: function(index, row) {
				this.editFormVisible = true
				if (row != undefined && row != 'undefined') {
					this.title = ''
					this.editForm.userId = row.userId
					this.editForm.userName = row.userName
					this.editForm.userRealName = row.userRealName
					this.editForm.roleId = row.roleId
					this.editForm.userMobile = row.userMobile
					this.editForm.userEmail = row.userEmail
					this.editForm.userSex = row.userSex
				} else {
					this.title = ''
					this.editForm.userId = ''
					this.editForm.userName = ''
					this.editForm.userRealName = ''
					this.editForm.roleId = ''
					this.editForm.userMobile = ''
					this.editForm.userEmail = ''
					this.editForm.userSex = ''
				}
			},
			// 
			submitForm(editData) {
				this.$refs[editData].validate(valid => {
					if (valid) {
						// 
						userSave(this.editForm)
							.then(res => {
								this.editFormVisible = false
								this.loading = false
								if (res.success) {
									this.getdata(this.formInline)
									this.$message({
										type: 'success',
										message: ''
									})
								} else {
									this.$message({
										type: 'info',
										message: res.msg
									})
								}
							})
							.catch(err => {
								this.editFormVisible = false
								this.loading = false
								this.$message.error('')
							})
					} else {
						return false
					}
				})
			},
			// 
			handleunit: function(index, row) {
				this.unitAccessshow = true
				let parms = 0
				UserDeptdeptTree(parms)
					.then(res => {
						if (res.data.success) {
							this.UserDept = this.changeArr(res.data.data)
						} else {
							this.$message({
								type: 'info',
								message: res.msg
							})
						}
					})
					.catch(err => {
						this.loading = false
						this.$message.error(',')
					})
			},
			handleClick(data, checked, node) {
				if (checked) {
					this.$refs.tree.setCheckedNodes([])
					this.$refs.tree.setCheckedNodes([data])
					this.unitparm.deptId = data.id
					this.unitparm.deptName = data.name
					//
				} else {}
			},
			// 
			unitPermSave() {
				let len = this.selectdata
				let ids = []
				if (len != 0) {
					for (let i = 0; i < len.length; i++) {
						ids.push(len[i].userId)
					}
				}
				this.unitparm.userIds = ids.join(',')
				UserChangeDept(this.unitparm)
					.then(res => {
						this.unitAccessshow = false
						if (res.success) {
							this.$message({
								type: 'success',
								message: ''
							})
							this.getdata(this.formInline)
						} else {
							this.$message({
								type: 'info',
								message: res.msg
							})
						}
					})
					.catch(err => {
						this.loading = false
						this.$message.error(',')
					})
			},
			// 
			selectChange(val) {
				this.selectdata = val
			},
			// 
			closeDialog(dialog) {
				if (dialog == 'edit') {
					this.editFormVisible = false
				} else if (dialog == 'perm') {
					this.dataAccessshow = false
				} else if (dialog == 'unit') {
					this.unitAccessshow = false
				}
			},
			// 
			deleteUser(index, row) {
				this.$confirm('?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning'
					})
					.then(() => {
						// 
						userDelete(row.id)
							.then(res => {
								if (res.success) {
									this.$message({
										type: 'success',
										message: '!'
									})
									this.getdata(this.formInline)
								} else {
									this.$message({
										type: 'info',
										message: res.msg
									})
								}
							})
							.catch(err => {
								this.loading = false
								this.$message.error('')
							})
					})
					.catch(() => {
						this.$message({
							type: 'info',
							message: ''
						})
					})
			},
			// 
			resetpwd(index, row) {
				this.resetpsd.userId = row.userId
				this.$confirm('?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning'
					})
					.then(() => {
						userPwd(this.resetpsd)
							.then(res => {
								if (res.success) {
									this.$message({
										type: 'success',
										message: ''
									})
									this.getdata(this.formInline)
								} else {
									this.$message({
										type: 'info',
										message: res.msg
									})
								}
							})
							.catch(err => {
								this.loading = false
								this.$message.error('')
							})
					})
					.catch(() => {
						this.$message({
							type: 'info',
							message: ''
						})
					})
			},
			// 
			dataAccess: function(index, row) {
				this.dataAccessshow = true
				this.saveroleId = row.userId
				UserDeptTree(row.userId)
					.then(res => {
						if (res.data.success) {
							this.checkmenu = this.changemenu(res.data.data)
							this.UserDept = this.changeArr(res.data.data)
						} else {
							this.$message({
								type: 'info',
								message: res.data.msg
							})
						}
					})
					.catch(err => {
						this.loading = false
						this.$message.error('')
					})
			},
			//
			changeArr(data) {
				var pos = {}
				var tree = []
				var i = 0
				while (data.length != 0) {
					if (data[i].pId == 0) {
						tree.push({
							id: data[i].id,
							name: data[i].name,
							pId: data[i].pId,
							open: data[i].open,
							checked: data[i].checked,
							children: []
						})
						pos[data[i].id] = [tree.length - 1]
						data.splice(i, 1)
						i--
					} else {
						var posArr = pos[data[i].pId]
						if (posArr != undefined) {
							var obj = tree[posArr[0]]
							for (var j = 1; j < posArr.length; j++) {
								obj = obj.children[posArr[j]]
							}

							obj.children.push({
								id: data[i].id,
								name: data[i].name,
								pId: data[i].pId,
								open: data[i].open,
								checked: data[i].checked,
								children: []
							})
							pos[data[i].id] = posArr.concat([obj.children.length - 1])
							data.splice(i, 1)
							i--
						}
					}
					i++
					if (i > data.length - 1) {
						i = 0
					}
				}
				return tree
			},
			// 
			changemenu(arr) {
				let change = []
				for (let i = 0; i < arr.length; i++) {
					if (arr[i].checked) {
						change.push(arr[i].id)
					}
				}
				return change
			},
			// -
			menuPermSave() {
				let parm = {
					userId: this.saveroleId,
					deptIds: ''
				}
				let node = this.$refs.tree.getCheckedNodes()
				let moduleIds = []
				if (node.length != 0) {
					for (let i = 0; i < node.length; i++) {
						moduleIds.push(node[i].id)
					}
					parm.deptIds = JSON.stringify(moduleIds)
				}
				UserDeptSave(parm)
					.then(res => {
						if (res.success) {
							this.$message({
								type: 'success',
								message: ''
							})
							this.dataAccessshow = false
							this.getdata(this.formInline)
						} else {
							this.$message({
								type: 'info',
								message: res.msg
							})
						}
					})
					.catch(err => {
						this.loading = false
						this.$message.error('')
					})
			},
			// 
			offlineUser(index, row) {
				this.$confirm('' + row.userName + '?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning'
					})
					.then(() => {
						userExpireToken(row.userName)
							.then(res => {
								if (res.success) {
									this.$message({
										type: 'success',
										message: '' + row.userName + ''
									})
									this.getdata(this.formInline)
								} else {
									this.$message({
										type: 'info',
										message: res.msg
									})
								}
							})
							.catch(err => {
								this.loading = false
								this.$message.error('')
							})
					})
					.catch(() => {
						this.$message({
							type: 'info',
							message: ''
						})
					})
			},
			// 
			refreshCache(index, row) {
				userFlashCache(row.userName)
					.then(res => {
						if (res.success) {
							this.$message({
								type: 'success',
								message: ''
							})
							this.getdata(this.formInline)
						} else {
							this.$message({
								type: 'info',
								message: res.msg
							})
						}
					})
					.catch(err => {
						this.loading = false
						this.$message.error('')
					})
			}
		}
	}
</script>

<style scoped>
	.user-search {
		margin-top: 20px;
	}

	.userRole {
		width: 100%;
	}
</style>



