<template>
	<div class="login-wrap">
		<el-form label-position="left" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm login-container">
			<h3 class="title"></h3>
			<el-form-item prop="name">
				<el-input type="text" v-model="ruleForm.name" auto-complete="off" placeholder=""></el-input>
			</el-form-item>
			<el-form-item prop="password">
				<el-input type="password" v-model="ruleForm.password" auto-complete="off" placeholder=""></el-input>
			</el-form-item>
			<!-- <el-row>
				<el-col :span="12">
					<el-form-item prop="code">
						<el-input type="text" v-model="ruleForm.code" auto-complete="off" placeholder="" @keyup.enter.native="submitForm('ruleForm')"></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12" class="code-box">
					<img :src="ruleForm.codeimg" alt="" class="codeimg" @click="getcode()">
				</el-col>
			</el-row> -->
			<el-checkbox class="remember" v-model="rememberpwd"></el-checkbox>
			<el-form-item style="width:100%;">
				<el-button type="primary" style="width:100%;" @click="submitForm('ruleForm')" :loading="logining"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>
<script type="text/ecmascript-6">
	import {
		login
	} from '../api/userMG'
	import {
		setCookie,
		getCookie,
		delCookie
	} from '../utils/util'
	import md5 from 'js-md5'
	export default {
		name: 'login',
		data() {
			return {
				//loadingfalse
				logining: false,
				// 
				rememberpwd: false,
				ruleForm: {
					//usernamepassword
					name: '',
					password: ''
					//code: '',
					// randomStr: '',
					// codeimg: ''
				},
				//rules
				rules: {
					name: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '',
						trigger: 'blur'
					}]
					// code: [{
					// 	required: true,
					// 	message: '',
					// 	trigger: 'blur'
					// }]
				}
			}
		},
		// ()
		created() {
			// this.$message({
			//   message: '',
			//   type: 'success'
			// })
			// 
			//this.getcode()
			// 
			this.getuserpwd()

		},
		// 
		methods: {
			// 
			getuserpwd() {
				if (getCookie('user') != '' && getCookie('pwd') != '') {
					this.ruleForm.name = getCookie('user')
					this.ruleForm.password = getCookie('pwd')
					this.rememberpwd = true
				}
			},
			//info
			submitForm(formName) {
				this.$refs[formName].validate(valid => {
					if (valid) {
						this.logining = true
						// 
						// setTimeout(() => {
						// 	this.logining = false
						// 	this.$store.commit('login', 'true')
						// 	this.$router.push({
						// 		path: '/system/user'
						// 	})
						// }, 1000)
						// 
						login(this.ruleForm).then(res => {
							//console.log(res)
							if (res != null) {
								if (this.rememberpwd) {
									//cookie7
									setCookie('user', this.ruleForm.name, 7)
									//cookie7
									setCookie('pwd', this.ruleForm.password, 7)
								} else {
									delCookie('user')
									delCookie('pwd')
								}
								//2
								setTimeout(() => {
									this.logining = false
									// token
									//localStorage.setItem('logintoken', res.data.token)
									// 
									//localStorage.setItem('userdata', JSON.stringify(res.data))
									this.$store.commit('login', 'true')
									this.$router.push({
										path: '/system/user'
									})
								}, 1000)
							} else {
								this.$message.error("!")
								this.logining = false
								return false
							}
						})
					} else {
						// 
						//this.getcode()
						this.$message.error('')
						this.logining = false
						return false
					}
				})
			},
		}
	}
</script>

<style scoped>
	.login-wrap {
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		padding-top: 10%;
		background-image: url(data:image/svg+xml;base64,);
		/* background-color: #112346; */
		background-repeat: no-repeat;
		background-position: center right;
		background-size: 100%;
	}

	.login-container {
		border-radius: 10px;
		margin: 0px auto;
		width: 350px;
		padding: 30px 35px 15px 35px;
		background: #fff;
		border: 1px solid #eaeaea;
		text-align: left;
		box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.1);
	}

	.title {
		margin: 0px auto 40px auto;
		text-align: center;
		color: #505458;
	}

	.remember {
		margin: 0px 0px 35px 0px;
	}

	.code-box {
		text-align: right;
	}

	.codeimg {
		height: 40px;
	}
</style>



